
body {
  background: #d6eaf8;
}

.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  min-height: 620px;
  grid-template-areas: "h h" "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  /* background-color: rgb(30, 195, 210); */
  background-color: #f5f6f7;
  /* background-color: #FAEBD7; */
}

